.about{
    max-width: 1140px;
    margin: auto;
    display: contents;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
    height: 66vh;
    @media (min-width: #{$screen-sm-min}) {
        display: grid;
    }
    .left,
    .right{
        margin: 0 20px;
    }
}
.my-image{
    width: 350px;
    height: 350px;
    transform: translate(-20px);
    transform: translate(86px, 10%);
    border: 6px solid $focus-color;
    background-color: black;

}

.image-div::after{
    content: "";
    width: 351px;
    height: 352px;
    position: relative;
    top: -325px;
    left: 91px;
    background-color: #0e5cde;
    opacity: 0.2;
    display: block;
}
.about-skills-list{
    display: inline-flex;
    list-style-type: square;
    padding-left: 19px;
    li{
        margin-right: 50px;
    }
}

.heading-1-medium{
    margin-left:14rem;
    font-size: 3rem;
}

.section-style{
    color: $focus-color;
    font-size: small;
    border-bottom:1px solid $focus-color;
    width: 27vw;
    display: inline-block;
}
.skills{
    transform: translateY(-94px);
}