$badass: #222;
$yellowColor:#ffa103;
$font-mono:"SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
$focus-color: #64ffda;
$background: rgb(10,25,47);
$heading-color: #ccd6f6;
@keyframes example {
  from {opacity: 0;}
  to {opacity: 1;}
}
body,html { 
  margin: 0; 
  padding: 0;
  padding-bottom: 0px;
  max-height: 100vh;
  background-color: rgb(10,25,47);
  scroll-behavior: smooth
}

.animation{
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: 1;
}
.text-align-center{
  text-align: center;
}

.text-align-left{
  text-align: left;
}

.subtitle{
  color: #8892b0;
}

.heading-1{
  color: $heading-color;
  font-size: 4rem;
  padding: 0.6rem 0 1rem;
  margin: 1rem;
  visibility: visible;
  animation-name: example;
   animation-duration: 4s;
   animation-iteration-count: 1;
}

.heading-2{
       
  color: $focus-color;
  font-family: $font-mono;
  line-height: 1.3;
  padding: 0px;
}

.padding{
  margin-left:13rem;
}

.white{
  color: white;
}