@keyframes example {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
body, html {
  margin: 0;
  padding: 0;
  padding-bottom: 0px;
  max-height: 100vh;
  background-color: rgb(10, 25, 47);
  scroll-behavior: smooth;
}

.animation {
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: 1;
}

.text-align-center {
  text-align: center;
}

.text-align-left {
  text-align: left;
}

.subtitle {
  color: #8892b0;
}

.heading-1 {
  color: #ccd6f6;
  font-size: 4rem;
  padding: 0.6rem 0 1rem;
  margin: 1rem;
  visibility: visible;
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: 1;
}

.heading-2 {
  color: #64ffda;
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", monospace;
  line-height: 1.3;
  padding: 0px;
}

.padding {
  margin-left: 13rem;
}

.white {
  color: white;
}

.MuiAppBar-root {
  position: fixed !important;
  z-index: 1 !important;
}

.MuiPopover-root {
  background-color: black;
  height: 35vh;
}

@media (max-width: 768px) {
  .MuiPopover-paper {
    top: 0 !important;
    left: 0 !important;
    min-width: 100% !important;
    max-height: 100% !important;
  }
}

.MuiList-root {
  padding: 0 !important;
}

.MuiMenuItem-root {
  color: white !important;
  justify-content: center !important;
}
@media (max-width: 768px) {
  .MuiMenuItem-root {
    background-color: black !important;
  }
}
.MuiMenuItem-root :hover {
  color: #e38007 !important;
}

MuiMenuItem-root {
  margin: 0 23px;
}

.desktopDisplay {
  display: flex;
  justify-content: space-around;
}

.portfolio {
  flex-grow: 18;
}
@media (min-width: 768px) {
  .portfolio {
    flex-grow: 3;
  }
}
.portfolio .MuiTypography-root {
  display: flex;
  align-items: center;
}

.logoname {
  display: flex;
}

.mask {
  width: 100%;
  height: 100%;
  position: relative;
  background-image: url("../assets/homeImage2.jpg");
  background: rgb(10, 25, 47);
  background: linear-gradient(198deg, rgb(10, 25, 47) 0%, rgb(17, 34, 64) 33%);
  min-height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.mask img {
  width: 100%;
  object-fit: cover;
}
@media (max-width: 768px) {
  .mask img {
    height: calc(100vh - 4px);
  }
}

.mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.btn {
  color: black !important;
  border-radius: 0px !important;
  width: 10rem;
  margin-bottom: 7px !important;
  margin-left: 1rem !important;
  height: 54px !important;
}
@media (min-width: 768px) {
  .btn {
    height: 64px !important;
    width: 18rem;
  }
}

.btn-light {
  background-color: transparent !important;
  color: #64ffda !important;
  border: 3px solid #64ffda !important;
}

.hero {
  height: 100%;
  width: 100%;
}

.content {
  color: white;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 55%;
  left: 50%;
  text-align: left;
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: 1;
}
.content h1 {
  font-size: 4rem;
  padding: 0.6rem 0 1rem;
  margin: 1rem;
  visibility: visible;
}
.content h3 {
  padding-bottom: 1rem;
  font-size: 23px;
  font-weight: lighter;
  margin-left: 1rem;
}

.footer {
  width: 100%;
  padding: 6rem 0;
  background-color: black;
  font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system, system-ui, sans-serif;
}
.footer h1 {
  font-size: clamp(40px, 5vw, 60px);
}

.footer-container {
  max-width: 1140px;
  margin: auto;
  display: contents;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
}
@media (min-width: 768px) {
  .footer-container {
    display: grid;
  }
}
.footer-container .left,
.footer-container .right {
  margin: 0 20px;
}

.copyrights {
  bottom: 0px;
  position: fixed;
  width: -webkit-fill-available;
  height: 34px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(19, 19, 19, 0.8);
  color: white;
}

.about {
  max-width: 1140px;
  margin: auto;
  display: contents;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
  height: 66vh;
}
@media (min-width: 768px) {
  .about {
    display: grid;
  }
}
.about .left,
.about .right {
  margin: 0 20px;
}

.my-image {
  width: 350px;
  height: 350px;
  transform: translate(-20px);
  transform: translate(86px, 10%);
  border: 6px solid #64ffda;
  background-color: black;
}

.image-div::after {
  content: "";
  width: 351px;
  height: 352px;
  position: relative;
  top: -325px;
  left: 91px;
  background-color: #0e5cde;
  opacity: 0.2;
  display: block;
}

.about-skills-list {
  display: inline-flex;
  list-style-type: square;
  padding-left: 19px;
}
.about-skills-list li {
  margin-right: 50px;
}

.heading-1-medium {
  margin-left: 14rem;
  font-size: 3rem;
}

.section-style {
  color: #64ffda;
  font-size: small;
  border-bottom: 1px solid #64ffda;
  width: 27vw;
  display: inline-block;
}

.skills {
  transform: translateY(-94px);
}

.work {
  margin-top: 4rem;
  padding: 4rem;
  background: linear-gradient(198deg, rgb(10, 25, 47) 0%, rgb(17, 34, 64) 33%);
}

.projects {
  padding-top: 10rem;
  padding-left: 4rem;
}

.project-card {
  width: 250px;
  height: 250px;
  background-color: #112240;
  border: 1px solid #112240;
  border-radius: 12px;
  padding: 20px;
  text-align: center;
  align-items: center;
  display: grid;
  margin: 10px;
  min-width: 250px;
}

.project-card:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-container {
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}

